body {
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  margin-bottom: 20px;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.img {
  max-width: 100px;
  width: 100%;
  margin: 10px;
}

.photo-card {
  width: auto;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
  display: block;
}

.load-more:hover {
  background-color: #2980b9;
}

.search-form input {
  width: 300px;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #27ae60;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.search-form {
  text-align: center;
  margin-top: 50px;
}

